<template>
  <div>
    <h2>分类管理</h2>
    <a-row type="flex" style="margin-bottom: 20px;">
      <a-col style="width: 100%; display: flex;">
        <!-- <div style="width: 100%;">
          <a-input v-model="searchParams.keyword" style="max-width: 200px; margin-right: 12px;" />
          <a-button type="primary">搜索</a-button>
        </div> -->
        <a-button type="primary" @click="popCategory">添加分类</a-button>
      </a-col>
    </a-row>
    <s-table
      ref="table"
      bordered
      size="default"
      row-key="id"
      :columns="columns"
      :data="loadData"
    >
      <template slot="action" slot-scope="text">
        <a-button type="link" @click="toEdit(text.id)">编辑</a-button>
        <a-button type="link" @click="toDelete(text.id)">删除</a-button>
      </template>
    </s-table>

    <a-modal
      v-model="categoryVisible"
      :title="isEdit ? '编辑分类':'添加分类'"
      width="60%"
      @ok="handleSubmit"
      :okButtonProps="{
        props: {
          loading: submitLoading
        }
      }"
    >
      <a-spin :spinning="modalLoading">
        <a-form>
          <a-form-item label="分类名称">
            <a-input v-model="form.category_name" />
          </a-form-item>
        </a-form>
      </a-spin>
    </a-modal>
  </div>
</template>
<script>
import STable from '@/components/Table'
import { mapState, mapActions } from 'vuex'
const getDefaultSearchParams = () => ({
  keyword: ''
})
const getFormDefaultParams = () => ({
  category_name: ''
})
export default {
  components: { STable },
  data() {
    const columns = [
      {
        title: 'id',
        dataIndex: 'id'
      },
      {
        title: '标签名称',
        dataIndex: 'category_name'
      },
      {
        title: '操作',
        scopedSlots: { customRender: "action"},
      }
    ]
    return {
      columns,
      searchParams: getDefaultSearchParams(),
      form: getFormDefaultParams(),
      submitLoading: false,
      isEdit: false,
      categoryVisible: false,
      modalLoading: false,
      loadData: paramters => {
        return this.getCategoryList(paramters)
      }
    }
  },
  methods: {
    ...mapActions('category', {
      getCategoryList: 'getList',
      deleteCategory: 'delete',
      updateCategory: 'update',
      addCategory: 'add',
      getCategoryDetail: 'getDetail'
    }),
    popCategory() {
      this.isEdit = false
      this.form = getFormDefaultParams()
      this.categoryVisible = true
    },
    toEdit(id) {
      this.form = getDefaultSearchParams()
      this.isEdit = true
      this.modalLoading = true
      this.categoryVisible = true
      this.getCategoryDetail(id).then(res => {
        this.form = Object.assign({}, this.form, res.data)
      }).finally(() => {
        this.modalLoading = false
      })
    },
    toDelete(id) {
      const _this = this
      this.$confirm({
        content: '你确定要删除这个分类？',
        okText: '确认',
        cancelText: '取消',
        onOk() {
          _this.deleteCategory(id).then(res => {
            _this.$refs.table.refresh(true)
          })
        }
      });
    },
    handleSubmit() {
      if (this.modalLoading) {
        this.categoryVisible = false
        return ;
      }
      this.submitLoading = true
      if (this.isEdit) {
        this.updateCategory({
          id: this.form.id,
          data: this.form
        }).then(() => {
          this.$message.success('修改成功')
          this.$refs.table.refresh(true)
          this.categoryVisible = false
        }).finally(() => { 
          this.submitLoading = false 
          })
      } else {
        this.addCategory(this.form).then(() => {
          this.$message.success('添加成功')
          this.$refs.table.refresh(true)
          this.categoryVisible = false
        }).finally(() => { 
          this.submitLoading = false 
          })
      }
    }
  }
}
</script>